.usa-button-secondary {
  background-color: white !important;
}

.btn-unstyled {
  background: transparent;
  border: 0;
  color: $color-primary;
  font-weight: normal;
  line-height: 1.5;
  margin: 0;
  padding: 0;
  text-decoration: underline;
  width: auto;

  &:hover,
  &:active {
    background: transparent;
    color: $color-primary-darker;
    text-decoration: none;
  }
}

.btn-favorite {
  background: transparent;
  display: block;
  height: units(4);
  margin: 0;
  padding: 0;
  width: units(4);

  &:hover,
  &:active {
    background: transparent;
  }

  svg {
    display: block;
    height: units(4);
    width: units(4);
  }

  .unselected {
    color: $color-gray;
  }
  .selected {
    color: $color-gold;
  }

}