.cards {
  display: flex;
  flex-wrap: wrap;
  list-style: none;
  margin: 0;
  padding: 0;

  > li {
    margin: 0;
  }
}

.card {
  align-items: flex-start;
  background-color: $color-gray-light-alt;
  display: flex;
  flex-wrap: wrap;
  margin: 0;
  padding: units(3);

  .usa-checkbox__label {
    margin-top: 0;
  }
}

.card__star {
  margin-left: auto;

  .btn-favorite {
    margin-left: auto;
  }
}

.card__heading {
  margin: 0;
}

.card__actions {
  width: 100%;
}